import React, { useState, useCallback } from "react";
import { Card, List, Avatar, Checkbox, Button } from "antd";
import { DeleteOutlined } from "@ant-design/icons";
import {defaultTodos, features, todoAvatars, typeSwap} from "../constants";
import FeaturesTour from "./features-tour/FeaturesTour";

export default function Todo() {
  const [todos, setTodos] = useState(defaultTodos);
  const toggleTaskType = useCallback((id) => () => {
    setTodos(
      todos.map((todo) => {
        if (todo.id === id) {
          return {
            ...todo,
            type: typeSwap[todo.type],
          };
        }
        return todo;
      })
    );
  });
  const toggleTodo = useCallback((id) => () => {
    setTodos(
      todos.map((todo) => {
        if (todo.id === id) {
          return {
            ...todo,
            done: !todo.done,
          };
        }
        return todo;
      })
    );
  });
  const deleteTodo = useCallback((id) => () => {
    setTodos(
      todos.filter((todo) => {
        return todo.id !== id;
      })
    );
  });

  return (
    <FeaturesTour features={features}>
      <Card style={{ margin: "0 auto", width: "60vw" }}>
        <List
          itemLayout="horizontal"
          dataSource={todos}
          renderItem={(todo) => (
            <List.Item>
              <List.Item.Meta
                avatar={
                  <Avatar
                    className="todo-avatar"
                    style={{ cursor: "pointer" }}
                    onClick={toggleTaskType(todo.id)}
                    src={todoAvatars[todo.type]}
                  />
                }
                title={todo.title}
                description={todo.description}
              />
              <div>
                <Checkbox onChange={toggleTodo(todo.id)}>Completed</Checkbox>

                <Button
                  className="todo-delete"
                  style={{ marginLeft: "5px" }}
                  onClick={deleteTodo(todo.id)}
                  type="primary"
                  danger
                >
                  <DeleteOutlined />
                </Button>
              </div>
            </List.Item>
          )}
        />
      </Card>
    </FeaturesTour>
  );
}
