import React, { useRef, useEffect, useState } from "react";
import ReactDOM from "react-dom";
import "./styles.css";

const defaultStyles = {
  position: 'absolute',
  zIndex: 100,
};

const getTooltipStyles = (target, tooltip, position) => {
  const margin = 10;

  const {
    top: targetTop,
    left: targetLeft,
    width: targetWidth,
    height: targetHeight,
  } = target.getBoundingClientRect();

  const {
    width: tooltipWidth,
    height: tooltipHeight,
  } = tooltip.getBoundingClientRect();

  switch (position) {
    case 'bottom':
      return {
        left: targetLeft + (targetWidth / 2 - tooltipWidth / 2),
        top: targetTop + targetHeight + margin,
      }
    default:
      return {
        left: targetLeft + (targetWidth / 2 - tooltipWidth / 2),
        top: targetTop - tooltipHeight - margin,
      }
  }
};

const Tooltip = ({
  show,
  target,
  title,
  message,
  position,
  isLast,
  onNext,
  onSkip
}) => {
  const tooltipNode = useRef(null);
  const [styles, setStyles] = useState(defaultStyles);

  useEffect(() => {
    const selector = document.querySelector(target);
    const handleResize = () => {
      window.requestAnimationFrame(() => {
        setStyles(curStyles => ({
          ...curStyles,
          ...getTooltipStyles(selector, tooltipNode.current, position)
        }));
      });
    }
    if (selector && tooltipNode.current) {
      tooltipNode.current.focus();
      window.addEventListener('resize', handleResize);
      handleResize();
    } else {
      onNext();
    }
    return () => window.removeEventListener('resize', handleResize);
  }, [target, position, onNext]);

  if (!show) return null;

  return ReactDOM.createPortal(
    <div className="tooltip-wrapper">
      <div
        tabIndex={-1}
        className={`tooltip-box ${position || 'top'}`}
        ref={tooltipNode}
        style={styles}>
          <h3 className="tooltip-title">{title}</h3>
          <p className="tooltip-message">{message}</p>
          {!isLast &&
            <button
              type="button"
              className="tooltip-next"
              onClick={onNext}>
                Next
            </button>
          }
          <button
            type="button"
            className="tooltip-skip"
            onClick={onSkip}>
              {isLast ? 'Finish' : 'Skip'}
          </button>
      </div>
    </div>,
    document.body
  );
}

export default Tooltip;
