import React, { useState, useEffect } from "react";
import ReactMarkdown from "react-markdown";
import { Layout, Menu, Breadcrumb, Button } from "antd";
import Todo from "./components/Todo"
import InstructionsMd from "./instructions.md";
import "./App.css";

const { Header, Content, Footer } = Layout;

const resetTour = () => {
  localStorage.removeItem('show-features-tour');
  window.location.reload();
}

/*
  Note: You shouldn't need to modify this!
*/
const renderTab = (tab, instructions) => {
  return {
    instructions: (
      <div style={{ width: "600px", margin: "auto" }}>
        <ReactMarkdown source={instructions} />
      </div>
    ),
    todo: (
      <>
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item>Product</Breadcrumb.Item>
          <Breadcrumb.Item>Weekly</Breadcrumb.Item>
          <Breadcrumb.Item>Tasks</Breadcrumb.Item>
        </Breadcrumb>
        <Todo />
        <div style={{ textAlign: 'center', marginTop: 30 }}>
          <Button className="reset-tour" onClick={resetTour}>Reset features tour</Button>
        </div>
      </>
    ),
  }[tab];
};

function App() {
  const [tab, setTab] = useState("instructions");
  const [instructions, setInstructions] = useState("");

  useEffect(() => {
    fetch(InstructionsMd)
      .then((resp) => resp.text())
      .then((text) => setInstructions(text));
  }, []);

  return (
    <Layout style={{ height: "100vh" }}>
      <Header style={{ position: "fixed", zIndex: 1, width: "100%" }}>
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={[tab]}>
          <Menu.Item key="instructions" onClick={() => setTab("instructions")}>
            Instructions
          </Menu.Item>
          <Menu.Item key="todo" onClick={() => setTab("todo")}>
            Todo App
          </Menu.Item>
        </Menu>
      </Header>
      <Content
        className="site-layout"
        style={{ padding: "25px 75px", marginTop: 64 }}
      >
        {renderTab(tab, instructions)}
      </Content>
      <Footer style={{ textAlign: "center" }}>
        Made by PopSQL ðŸ˜ƒ
      </Footer>
    </Layout>
  );
}

export default App;
