import React, { useState, useCallback } from "react";
import Tooltip from "./Tooltip";

const FeaturesTour = ({ features = [], children }) => {
  const [index, setIndex] = useState(0);
  const [visible, setVisible] = useState(!localStorage.getItem('show-features-tour'));
  const activeFeature = features[index] || {};
  const isLast = index === (features.length - 1);

  const onSkip = useCallback(() => {
    setVisible(false);
    localStorage.setItem('show-features-tour', 'false');
  }, []);

  const onNext = useCallback(() => {
    setIndex((curIndex) => {
      const newIndex = curIndex + 1;
      if (newIndex > features.length - 1) {
        onSkip();
        return curIndex;
      }
      return newIndex;
    });
  }, [features, onSkip]);

  return (
    <>
      <Tooltip
        show={visible}
        target={activeFeature.selector}
        position={activeFeature.position}
        title={activeFeature.title}
        message={activeFeature.message}
        isLast={isLast}
        onSkip={onSkip}
        onNext={onNext}
      />
      {children}
    </>
  )
}

export default FeaturesTour;
